import { Breadcrumb } from 'antd';
import React, { useState } from "react";
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { Avatar } from "antd";
import SearchIcon from '@mui/icons-material/Search';

import image91 from "../../assets/images/image 91.png";
import Rectangle305 from "../../assets/images/Rectangle 305.png";
import Ellipse24 from "../../assets/images/Ellipse 24.png";

import { creatorData } from "../../constant/creator_data";
import { requestData } from "../../constant/request_data";
import SearchBox from '../../components/common/searchBox';
import { PaginationButtons } from '../../components/common/pagination';

function Requestsearch() {
    const [age, setAge] = useState('');
    const { t, i18n } = useTranslation();
    const navigate = useNavigate();
    const handleChange = (event) => {
        setAge(event.target.value);
    };

    return (
        <>
            {/* <Header page={"webscreens"} pagetitle={"Client"} /> */}
            <section id="main-section">
                <div className="py-12">
                    <Breadcrumb
                        className="p-5"
                        separator=">"
                        items={[
                            {
                                title: 'TOP_PAGE',
                                href: '/'
                            },
                            {
                                title: 'ABOUT_US',
                            },
                        ]}
                    />

                    <SearchBox />
                    <div className="cart-bar w-2/5">
                        <p>#JAPAN </p>
                    </div>
                    <div className="mt-16 flex items-center gap-7.5 flex-wrap justify-center my-5">
                        {requestData.map((request, index) => (
                            <div key={index} id="card" onClick={() => { navigate('/request-designer') }} className="cursor-pointer border bg-box rounded-xl border-gray-200 shadow-md shadow-gray-400 w-full flex md:flex-row flex-col justify-between md:px-12 p-3 md:py-6 mt-5" >
                                <div className="relative">
                                    <p className="bg-blue-800 p-2 text-white w-32 rounded-md text-center font-bold text-sm my-2">{request.status}</p>
                                    <p className="text-badge font-bold text-xl">■REQUEST■ <br />
                                        {request.title}</p>
                                    <p className="font-semibold text-xl md:text-2xl mt-5">{request.tags}</p>
                                    <div className="flex items-center gap-2.5 font-bold text-gray-800 mt-20">
                                        <Avatar className="absolute bottom-2 w-12 h-12 rounded-full" src={request.userImage} alt="user" />
                                        <p className="absolute bottom-4 left-16">{request.username}</p>
                                    </div>
                                </div>
                                <div className="relative md:p-12 p-2">
                                    <div className=" h-24 border-l-4 border-l-blue-800">
                                        <p className="mb-0 bg-blue-800 p-1 text-white rounded-md text-center font-bold text-xs-box-btn-tag">REWARD</p>
                                        <h1 className="mt-10 font-extrabold text-xl">{request.reward}</h1>
                                    </div>
                                    <p className="absolute bottom-0 right-0">掲載日：{request.date}</p>
                                </div>
                            </div>
                        ))}
                    </div>
                    <PaginationButtons />
                    <div className=" shadow-md shadow-gray-200">
                        <div className="cart-bar w-2/5 my-10">
                            <p>#CREATER</p>
                        </div>
                        <div className="flex items-center gap-7 flex-wrap justify-center p-10  my-5">
                            {creatorData.map((creator, index) => (
                                <div key={index} onClick={() => { navigate("/requester/profile") }} id="card" className="w-52 shadow-md shadow-gray-600 bg-white">
                                    <div className="relative">
                                        <img className="w-full h-44" src={creator.mainImage} alt="main" />
                                        <p className="absolute top-2 left-4 bg-gray-700 text-white font-bold rounded-lg px-10 py-[1px]">REQUESTER</p>
                                        <p className="bg-blue-800 text-white absolute rounded-lg text-xs top-9 left-4 px-3 py-1">{creator.availability}</p>
                                    </div>
                                    <img className="w-20 h-20 rounded-full shadow-md shadow-gray-600 mt-[-40px] ml-16 absolute" src={creator.userImage} alt="user" />
                                    <h1 className="pt-16 text-center text-[15px] font-bold">{creator.username}</h1>
                                    <p className="pb-2 pt-5 text-center text-[7px] font-semibold">{creator.description}</p>
                                </div>
                            ))}
                        </div>
                    </div>
                    <PaginationButtons />
                </div>
            </section>
        </>
    );
}

export default Requestsearch;
